@import 'main';
// End Of Import

.navover{
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0, .8);
    z-index: 9999;
    opacity: 0;
    visibility: hidden;
    transition: .5s;

    &.open{
        opacity: 1;
        visibility: visible;
    }
}

.sideNav{
    padding: 24px;
    position: fixed;
    top: 0;
    right: -100%;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 9999;
    overflow: auto;
    transition: .5s;

    &.open {
        right: 0;
    }

    .opt{
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 40px;

        .lang{


            a{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 30px;
                height: 30px;
                color: #000;
                font-size: 15px;
            }
        }

        .xtr{
            position: relative;
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: auto;
            margin-right: 10px;

            .srch{
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;

                svg{

                    path{
                        fill: #000;
                    }
                }
            }

            form{
                opacity: 0;
                visibility: hidden;
                z-index: 99;
                position: fixed;
                top: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(0,0,0, .8);
                display: flex;
                align-items: center;
                justify-content: center;
                transition: .5s;
                display: none;

                @include Sm{
                    display: flex;
                }

                &.open{
                    opacity: 1;
                    visibility: visible;

                    button{
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-45%);
                        margin-left: 10px;
                        width: 30px;
                        height: 30px;

                        svg{
                            margin-top: 5px;
                        }
                    }

                    input{
                        display: block;
                    }
                }

                .icon{
                    position: absolute;
                    top: 15%;

                    svg{

                        path{
                            fill: #fff;
                        }
                    }
                }

                .cont{
                    position: relative;
                    top: -15%;
                }

                button{
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    border: 0;

                    svg{


                        path{
                            fill: #000;
                        }
                    }
                }

                input{
                    border: 0;
                    border-radius: 5px;
                    width: 600px;
                    height: 45px;
                    padding: 0 25px;
                    background-color: $color-white;
                    display: none;

                    @include Xs{
                        width: 400px;
                    }

                    @include mob375{
                        width: 290px;
                    }
                }
            }
        }

        .close1{
            font-size: 20px;
            color: #84848445;
            cursor: pointer;
            display: flex;
            align-items: center;
        }
    }

    .links{
        display: block;

        .list{
            display: flex;
            align-items: center;
            flex-direction: column;
            height: 100%;
            overflow: auto;

            & > li{
                padding: 7px 15px;
                width: 100%;

                a{
                    width: 100%;
                    display: block;
                    font-size: 16px;
                    color: $color-sec;
                    line-height: 1.6;
                    transition: .5s;                

                    &:hover{
                        color: $color-black;
                    }
                }

                &:not(:last-child){
                    border-bottom: 1px solid #dedede;
                }
            }
        }
    }
}

// Mob Nav

// Nav
nav{
    padding: 20px 0;
    background-color: #FFF;
    position: sticky;
    top: 0;
    z-index: 9;
    transition: .5s;

    .container{
        position: relative;

        .lang{
            position: absolute;
            top: -15px;
            left: 15px;
            font-size: 14px;
            color: #bdbdbd;
            transition: .5s;

            &:hover{
                color: #000;
            }

            @include Sm{
                display: none;
            }
        }
    }

    @include mob480{
        padding: 15px 0;
    }
    
    .content-top{
        display: flex;
        align-items: center;
        overflow: hidden;
    }

    &.scroll{
        padding: 30px 0;
        margin-top: 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 999;
        background-color: #FFF;
        box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
        transition: .5s;

        @include mob480{
            padding: 15px 0;
        }

        .logo{
            img{
                filter: brightness(0);
            }
        }

        .links{

            li{

                a{
                    color: #000;
                }
            }
        }

        .search{

            svg{

                path{
                    fill: #000;
                }
            }

            input{
                color: #000 !important;
            }
        }

        .bars{

            span{
                background-color: #000;


                &:after,&:before{
                    background-color: #000;
                }
            }
        }
    }

    .logo{
        margin-left: auto;

        img{
            filter: brightness(0);
        }
    }

    .links{
        transition: .5s;

        @include Sm{
            display: none;
        }

        &.open{
            transform: translateX(200px);
            opacity: 0;
            visibility: hidden;
        }

        ul{

            display: flex;


            li{


                a{
                    font-size: 17px;
                    font-family: "frtBold";
                    color: #000;
                    transition: .5s;

                    &:hover{
                        color: #bdbdbd;
                    }
                }

                &:not(:last-child){
                    margin-left: 30px;
                }
            }
        }
    }

    .search{
        margin-right: 45px;

        svg{

            path{
                fill: #000;
            }
        }

        @include Sm{
            display: none;
        }

        .over{
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            visibility: hidden;
            z-index: 99;

            &.open{
                opacity: 1;
                visibility: visible;
            }
        }

        form{
            position: relative;

            & > button{
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
                transition: .5s;


                &.hide{
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }

        .serach-content{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 200px;
            transition: .5s;

            &.open{
                right: calc(-200px - -31px);
                z-index: 99;
            }

            input{
                width: 200px;
                height: 30px;
                background-color: transparent;
                border: 0;
                border-bottom: 1px solid #ccc;
                color: #000;

                &::placeholder{
                    color: rgb(212, 212, 212);
                }
            }

            button{
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: 0;
                padding: 0;
                margin: 0;
                background-color: transparent;
                border: 0;
            }
        }
    }

    .bars{
        width: 30px;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 15px;
        margin-bottom: 7px;
        cursor: pointer;
        display: none;

        @include Sm{
            display: flex;
        }

        span{
            width: 100%;
            height: 2px;
            background-color: #000;
            position: relative;
            display: block;

            &:after,&:before{
                content: '';
                width: 30px;
                height: 2px;
                position: absolute;
                right: 0;
                background-color: #000;
            }

            &:before {
                top: 8px;
            }

            &:after {
                top: -8px;
            }
        }
    }
}
// Nav

// Start Bread

.bread{
    height: 100px;
    background-image: url("../images/bread.jpg");
    background-size: cover;
    background-position: center;

    img{
        position: absolute;
        top: 0;
        right: 0;
        z-index: -9;
        height: 100%;
        width: 100%;
    }

    .container{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }

    p{
        margin: 0;
        font-family: "frtLight";

        a{
            position: relative;
            color: $color-white;
            font-family: inherit;


            &:not(:last-child) {
                margin-left: 26px;
                color: #686868;


                &:after{
                    content: "/";
                    transform: translateX(-13px);
                    display: inline-block;
                }
            }
        }
    }
}

// End Of Bread

// Start Of Overview

.about{
    padding: 70px 0;

    @include Sm{
        padding: 50px 0;
    }

    @include Xs{
        padding: 35px 0 20px;
    }

    .rtContent{
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        padding-right: 15px;

        @include Sm{
            text-align: center;
        }

        @include Xs{
            padding: 0;
        }

        h2{
            font-size: 24px;
            font-family: "frtBlk";
            line-height: 1.787;
            color: #000;
            margin-bottom: 30px;
        }

        img{
            display: none;

            @include Sm{
                display: block;
                width: max-content;
                margin: auto;
            }
        }

        p{
            margin-bottom: 0px;
            color: #3f3f3f;
            line-height: 1.765;
            font-size: 17px;
            font-family: "frtLight";              

            @include Sm{
                margin-top: 25px;
            }

            &:first-child{
                font-size: 20px;
                font-family: 'frtLight';
                margin-bottom: 25px;
                padding-right: 20px;
                line-height: 1.2;
                position: relative;
                width: max-content;

                @include Sm{
                    margin: auto;
                    margin-bottom: 25px;
                    width: max-content;
                }


                &:after{
                    content: "";
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 1px;
                    height: 100%;
                    background-color: #c0c0c0;
                }
            }
        }

        a{
            font-size: 16px;
            font-family: "frtLight";
            color: #b5b5b5;
            width: max-content;
            text-decoration: none;
            transition: .5s;

            @include Sm{
                display: block;
                margin: auto;
            }

            &:hover{
                color: rgb(81, 81, 81);
            }
        }
    }

    .ltContent{

        img{
            width: 100%;

            @include Sm{
                display: none;
            }
        }
    }
}

.why-us{
    padding: 80px 0;
    background-image: url("../images/clients.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    @include Xs{
        padding: 40px 0;
    }


    .title{
        text-align: center;
        color: #000;

        p{
            font-size: 20px;
            font-family: "frtLight";
            position: relative;
            width: max-content;
            margin: auto;
            margin-bottom: 15px;

            &:after,&:before{
                content: "";
                position: absolute;
                top: 65%;
                display: block;
                width: 20px;
                height: 1px;
                background-color: #c0c0c0;
            }

            &:after{
                right: -40px;
            }

            &:before{
                left: -40px;
            }
        }

        h2{
            font-size: 24px;
            font-family: "frtBlk";
            margin-bottom: 35px;
        }
    }

    .col-md-6{
        &:first-child{
            @include Xs{
                margin-bottom: 30px;
            }
        }
    }

    .why-box{
        background-color: #FFF;
        padding: 40px;
        text-align: center;
        box-shadow: 0px 0px 25px 0px rgba(1, 1, 1, 0.1);
        transition: .5s;

        &:hover{
            transform: translateY(-10px);
        }

        .img{
            width: 42px;
            height: 42px;
            margin: auto;
            margin-bottom: 20px;
        }

        h3{
            font-size: 20px;
            font-family: "frtBold";
            color: #000;
        }

        p{
            font-size: 16px;
            color: rgb(0, 0, 0);
            line-height: 1.563;
            font-family: "frtLight";              
        }
    }
}

// End Of Overview

// Members

.members{
    padding: 80px 0 100px;
    text-align: center;

    @include Xs{
        padding: 50px 0 50px;
    }

    h2{
        font-size: 24px;
        font-family: "frtBlk";
        margin-bottom: 30px;
    }


    .member{
        border: 1px solid #e7e7e7;
        padding: 40px 18px;
        text-align: center;
        margin-bottom: 30px;
        transition: .5s;

        &:hover{
            transform: translateY(-10px);
        }

        &.bottom{
            margin-top: 30px;
        }

        .pic{
            width: 150px;
            height: 150px;
            margin: auto;
            border-radius: 50%;
            background-color: #f5f5f5;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 25px;
            overflow: hidden;

            i{
                font-size: 48px;
                color: #d5d5d5;
            }
        }

        p{
            font-size: 18px;
            font-family: "frtBold";
            margin-bottom: 10px;
        }

        span{
            font-size: 15px;
            font-family: "frtLight";
        }
    }
}

// Members




footer{
    background-color: #000;

    & > .container{
        padding: 50px 0;
        border-bottom: 1px solid #1a1a1a;

        @include mob480{
            padding: 30px 0;
        }
    }

    .content{
        display: flex;
        align-items: center;

        @include Sm{
            flex-direction: column;
            justify-content: center;
        }
    }

    .details{
        display: flex;
        margin-right: auto;
        color: $color-white;

        @include Sm{
            margin-right: unset;
            margin-top: 35px;
        }

        @include Xs{
            flex-direction: column;
            align-items: center;
            text-align: center;
        }

        i{
            font-size: 15px;
            margin-left: 10px;
        }

        h3{
            font-size: 17px;
            font-family: "frtBlk";

            & + p{
                margin-bottom: 0;
                font-size: 15px;
                font-family: "frtLight";
            }
        }

        a{
            font-size: 15px;
            font-family: "frtLight";
            margin-bottom: 0;
            color: currentColor;
        }
    }

    .detail{

        &:not(:last-child){
            margin-left: 60px;

            @include Xs{
                margin-left: 0;
                margin-bottom: 25px;
                text-align: center;
            }
        }
    }
}

// End Of Footer

// End

.copy{
    background-color: #000;
    font-size: 13px;
    color: rgb(189, 188, 188);
    padding: 10px 0;

    .container{
        padding: 0;
    }

    .cont{
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include Xs{
            padding: 0 15px;
            flex-direction: column-reverse;
            align-items: center;
        }
    }


    .content{
        display: flex;
        align-items: center;
        color: #696969;
        font-size: 13px;

        p{
            margin-bottom: 0;

            @include mob375{
                display: flex;
                flex-direction: column;
                align-items: center;
            }
        }

        a{
            color: #696969;
    
            img{
                margin-right: 5px;
            }

            @include mob375{
                margin-top: 10px;
            }
        }
    }

    .links{

        @include Xs{
            margin-bottom: 15px;
        }
        

        ul{
            display: flex;

            li{
                &:not(:last-child){
                    margin-left: 20px;
                }

                a{
                    font-size: 13px;
                    font-family: 'frtLight';
                    color: #686868;
                    transition: .5s;


                    &:hover{
                        color: #FFF;
                    }
                }

                  
            }
        }
    }

}

